.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.search-bar {
  margin: 25px;
  text-align: center;
}

.search-bar input {
  width: 75%;
  border-radius: 4px  
}

.video-item img {
  max-width: 64px;
}

.video-detail .details {
  margin-top: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px
}

.embed-responsive {
  border-radius: 4px
}

.list-group-item {
  cursor: pointer;
}

.list-group-item:hover {
  background-color: #eee
}

.media-object {
  border-radius: 4px;
}


